
/*  FOOTER  */
#footer-wrapper{
    color: #757575;  
    margin: 4.5rem auto 0 auto;
    padding: 0;
}
#footer-center-wrapper{    
    height: auto;   
    
    max-width: 56rem;
    display: block;
    margin: 0 auto;
}
/*  footer links */
#contact-number{   
   color: inherit;
   font-weight: 600;
   margin-bottom: 1.7rem;   
   margin-left: auto;
}
#contact-number-link{
    color: inherit;
    text-decoration: none;
}
#footer-center-wrapper a:hover{
    text-decoration: underline;
}
#footer-links{
    list-style-type: none;        
    display: flex;
    flex-wrap: wrap;    
    font-weight: 500;
    padding: 0;
}
.footer-link-container{     
    width: 25%;
    min-width: 135px;
    margin-bottom: 0.63rem;
    
}
.footer-link-element{
    color: inherit;
    text-decoration: none;
    font-size: 0.8rem;
}
/*  /footer links*/
/*  language selector  */
#language-selector{
    position: relative;    
    width: 8.5rem;    
}
#language-selector::before{
    content: url(../../image/globe-icon.png);
    position: absolute;
    top: 55%;
    left: 15%;
    transform: translate(-50%, -50%)
}
#language-selector::after{
    content: url(../../image/language-arrow.png);
    position: absolute;
    top: 45%;
    left: 90%;
    transform: translate(-50%, -50%);
}
#lang-dropdown{
    color: #999;        
    border: 1px solid #333;
    border-radius: 2px;
    font-size: 1rem;
    background-color: black;
    width: inherit;
    height: 3.3rem;
    padding-left: 50px;   
    -moz-appearance:none;
    -webkit-appearance:none;
    appearance:none;
}
#footer-center-wrapper h6{
    font-size: .8rem;    
    margin: 1.5rem 0 2rem 0;
}
/*  /language selector */
/*  /FOOTER  */