.banner{ 
    background-size: cover;
    background-position: top;
    height: 70vh;
    color: #fff;
    margin-top: 10px;
}

.content{
    padding-top: 140px;
    height: 190px;
    margin-left: 5rem;
}

.title{
    font-size: 4rem;
    font-weight: 800;
    padding-bottom: 1rem;
}
.button{
    color: #fff;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: .5rem;
    padding-bottom: .5rem;
    background-color: rgba(51, 51, 51, .5);
    border:none;
    outline: none;
    cursor: pointer;
    margin-right: 1rem;
}
.button:hover{
    color: #111;
    background-color: #e6e6e6;
    transition-duration: .5s;
}
.discription{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 150px;
    max-width: 360px;
    font-weight: 200;
    overflow: hidden;
}
.fade{
    height: 14rem;
    background-image: linear-gradient(180deg,transparent,rgba(37,37,37,.61),#111);
    bottom: 0;
}