body{
    font-family: Nunito Sans, sans-serif;
    font-weight: bold;
    margin: 0;
    background-color: black;
    color: white;
    overflow-x: hidden;
}
.font-light{
    font-weight: 400;
}
.spacer{
    background-color: #222;
    height: .5rem;
}

/* HOME*/
#home-wrapper{          
    background-image: url(/netflix-clone-react/static/media/bg-titles.55011567.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: inset 0 0 1000px 100px  black;
    padding-bottom: 8rem;
}
#center-wrapper{   
    margin: 10.65rem auto 0;
    max-width: 50rem;
    text-align: center;    
}
#center-wrapper h1{
    margin: 0;
    font-size: 4.2em;
    line-height: 1em;
}
#center-wrapper h2{    
    margin: 1.12rem 0 0 0;
    font-size: 1.7em;
}

/* form */
.form-wrapper{       
    max-width: 47.62rem;    
    display: flex;
    flex-direction: column;    
}
.form-wrapper form{
    display: flex;
    padding-top: 1.75rem;
}
.email{
    width: 65.5%;
    height: 4.25rem;
    padding: 0;
    margin: 0 auto;
    border: 1px darkgrey solid;
    border-top-left-radius: 2px;
    border-bottom-left-radius: 2px;
    text-indent: 0.62rem;
    font-size: 1.1rem;    
}
.email-submit{
    width: 34.2%;  
    height: 4.37rem;    
    margin: 0 auto;
    border: none;
    border-top-right-radius: 2px;
    border-bottom-right-radius: 2px;
    border-left: 1px grey solid;
    background-color: #e50914;
    background-color: var(--netflix-red);    
    text-decoration: none;     
    display: flex;
    justify-content: center;
    align-items: center;
}

.email-submit:hover{
    background-color: #f40612;
    cursor: pointer;
}

.submit-div p{
    display: inline;
    color: white;
    padding: 0;
    margin: 0;
    padding-right: 0.5rem;
    font-size: 1.8rem;
}
.icon{
    height: 1.25rem;
}
.p-under-form{    
    font-size: 1.18em;
    font-weight: 400;
    text-align: center;
    width: 100%;
    margin-bottom: 0;
}
/* /form */
/* /HOME */

/* ENJOY TV */
/* sidetext */
.section-wrapper{
    padding: 1rem 0 3rem;
}
.section-center-wrapper{
    display: flex;
    justify-content: center;
    background-color: black;
    padding: 0 4rem;
}
.section-sidetext{    
    max-width: 34rem; 
    margin: auto 0;             
    padding: 2rem 3.5rem 0 1.5rem;   
    text-align: left;   
    text-align: initial;    
}
.section-sidetext h1{
    margin: 0 0 0.6rem;
    font-size: 3.2rem;
    line-height: 3.5rem;
}
.section-sidetext h2{
    font-size: 1.7rem;
    margin-bottom: 0;
}/*  /sidetext  */

/* tv animation*/
#tv-animation{
    max-width: 530px;
    width: 100%;
    height: 100%;    
    -webkit-transform: translateY(0%);    
            transform: translateY(0%);
    margin: auto 0;  
}
#tv-overlay{
    width: 100%;   
}
#tv-video{
    position: absolute;    
    width: 72.4%;    
    left: 13.3%;
    top: 20.6%;  
}/*  /tv animation*/
/*  /ENJOY TV  */

/*  DOWNLOAD  */
#mobile-wrapper{
    position: relative;
    order: 1;
    margin:auto 6rem auto 0;
    height: 100%;
    min-width: var(--min-width);

}
#download-sidetext{
    order: 2;
}
/*  mobile  */
#mobile{  
    max-width: 504px;
    width: 100%;    
}
#mobile-download{
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 10%;
    display: flex;    
    min-width: 264px;
    width: 60%;
    padding: .5rem;
    margin: 0 auto;
    border: 2px solid #444;
    border-radius: 13px;
    background-color: black;
    box-shadow: 0 0 15px 2px black;    
}
#show-cover{
    max-width: 3.57rem;   
}
#mobile-download-text{   
    max-width: 14rem;
    margin-left: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
}
#mobile-download-text h3{
    margin: 0;
    font-size: 1rem;
}
#mobile-download-text h4{
    margin: 0;
    font-size: .9rem;
    font-weight: lighter;
    color: #1c5cfc;
    
} /*  /mobile  */
/*  mobile animation  */
#mobile-download-animation{
    max-width: 1.5rem;
    width: 100%;
    align-self: center;
    margin-bottom: 1.5rem;
}
.animation-element{
    width: 1.5rem;
    position: absolute;
    right: 1rem;    
    opacity: 0;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

#download-icon{   
    -webkit-animation: download-animation 7s infinite;   
            animation: download-animation 7s infinite;  
    width: 1.2rem;    
}
@-webkit-keyframes download-animation{
    0%{opacity: 1;}
    18%{opacity: 1;}
    20%{opacity: 0;}
}
@keyframes download-animation{
    0%{opacity: 1;}
    18%{opacity: 1;}
    20%{opacity: 0;}
}
#loading-icon{   
    -webkit-animation: loading-animation 7s infinite;   
            animation: loading-animation 7s infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}
@-webkit-keyframes loading-animation{
    19%{opacity: 0;
        -webkit-transform: rotate(900deg);
                transform: rotate(900deg);}
    20%{opacity: 1;}
    78%{opacity: 1;}
    80%{opacity: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);}
}
@keyframes loading-animation{
    19%{opacity: 0;
        -webkit-transform: rotate(900deg);
                transform: rotate(900deg);}
    20%{opacity: 1;}
    78%{opacity: 1;}
    80%{opacity: 0;
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);}
}
#smartphone-icon{    
    -webkit-animation: smartphone-animation 7s infinite;    
            animation: smartphone-animation 7s infinite;
}
@-webkit-keyframes smartphone-animation{   
    79%{opacity: 0;}
    80%{opacity: 1;}
    99%{opacity: 1;}
    100%{opacity: 0;}
}
@keyframes smartphone-animation{   
    79%{opacity: 0;}
    80%{opacity: 1;}
    99%{opacity: 1;}
    100%{opacity: 0;}
}/* /mobile animation */
/* /DOWNLOAD  */


/*  DEVICES  */
#devices-animation{
    position: relative;   
    width: 100%;
    max-width: 550px;
    height: 100%;   
    margin: auto 0;
    padding-top: 2rem;
}
#devices-video{
    position: absolute;
    width: 61.23%;
    height: auto;
    right: 20%;
    top: 17%;
