}
#devices-overlay{
    position: relative;
    width: 100%;
    z-index: 1;
}
/*  /DEVICES  */


/*  FAQ  */
/* faq elements*/
#faq-wrapper{
    display: block;
    margin: 0 auto;   
}
#faq-wrapper-content{
    width: 45%;
    min-width: 700px;
    margin: 0 auto;
}
#faq-wrapper > h1{
