}
.faq-title img{
    float: right;
    width: 1.5rem;
    margin: 1.5rem;
} /*  /faq elements  */
/* faq form*/
#faq-form{
    margin: 2rem auto 0;
}/*  /faq form */
/* /FAQ   */



