/*  language selector  */
#language-selector{
    position: relative;    
    width: 8.5rem;    
}
#language-selector::before{
    content: url(data:image/png;base64,);
    position: absolute;
    top: 55%;
    left: 15%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%)
}
#language-selector::after{
    content: url(data:image/png;base64,);
    position: absolute;
    top: 45%;
    left: 90%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}
#lang-dropdown{
    color: #999;        
    border: 1px solid #333;
    border-radius: 2px;
    font-size: 1rem;
    background-color: black;
    width: inherit;
    height: 3.3rem;
    padding-left: 50px;
    -webkit-appearance:none;
    appearance:none;
}
#footer-center-wrapper h6{
