.all{
    height: 100vh;
    width: 100%;
    margin: 0;
    padding: 0;
    background: url("../../image/bg-titles.jpg") no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    box-shadow: inset 0 0 1000px 100px  black;
}
.wep{
    padding-top: 3rem;
    max-width: 800px;
    margin: auto;
    display: flex;
    flex-direction: column;
    gap: 20px;
    align-items: center;
    justify-content: center;
}
.wep button{
    font-size: 20px;
    color: #fff;
    border-radius: 5px;
    height: 3.5rem;
    width: 20rem;
}
.wep h2{
    font-size: 40px;
    line-height: 40px;
}
.wep h5{
    font-size: 22px;
    font-weight: 300;
    line-height: 27px;

}
.wep h2,h5{
    margin: 0;
    max-width: 60%;
}
@media screen and (max-width: 600px) {
    .wep h2 {
        font-size: 30px;
        line-height: 35px;
    }
    .wep h5 {
        font-size: 16px;
        line-height: 20px;
    }
    .email {
        width: 60%;
        max-height: 3.7rem;
    }
    .wep button {
        font-size: 16px;
        color: #fff;
        border-radius: 5px;
        height: 2.5rem;
        width: 13rem;
    }
}