.row{
    margin-left: 20px;
    color: #fff;
}
.row-title{
    padding-top: 10px;
}
.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}
.posters::-webkit-scrollbar{
    display: none;
}
.poster{
    cursor: pointer;
    max-height: 220px;
    margin-right: 10px;
}
.small-posters{
    cursor: pointer;
    max-height: 120px;
    margin-right: 10px;
}
.poster:hover{
    transform:scale(1.1);
}
.small-posters:hover{
    transform:scale(1.1);
}